import { Icon } from "@mui/material";
import { URLS, PARAMS } from "constants/api";
import Dashboard from "layouts/dashboard";
import Home from "layouts/home";
import { useCallback, useEffect, useState } from "react";

const generateRoute = (project) => ({
  type: "collapse",
  name: project.charAt(0).toUpperCase() + project.slice(1),
  key: project,
  icon: <Icon fontSize="small">assessment</Icon>,
  route: `/${project}`,
  component: <Dashboard project={project} />,
});

function useRoutes() {
  const [routes, setRoutes] = useState([
    {
      type: "collapse",
      name: "Home",
      key: "home",
      icon: <Icon fontSize="small">home</Icon>,
      route: "/home",
      component: <Home />,
    },
  ]);

  const fetchProjects = useCallback(async () => {
    const url = `${process.env.REACT_APP_API_URI}${URLS.getProjects}`;
    const response = await fetch(url, PARAMS.GET);
    const { result } = await response.json();
    const projects = [];
    result.map((elem) => projects.push(generateRoute(elem.project)));
    setRoutes((arr) => [...arr, ...projects]);
  });

  useEffect(() => {
    fetchProjects();
  }, []);

  return routes;
}

export default useRoutes;
